* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f8ccf9;
  overflow: hidden;
}

.btn {
  background-color: #ffffff;
  color: #7e4b8b;
  /* font-family: 'Montserrat', sans-serif; */
  text-transform: uppercase;
  margin: 15px;
  padding: 5px;
  border: 2px solid #7e4b8b;
  border-radius: 5px;
  box-shadow: 0 12px #883e9b, 0 18px rgba(0, 0, 0, 0.4);
  font-size: 60px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  position: relative;
  top: 0;
}

.disabled .btn {
  cursor: default;
}

.disabled .btn:hover {
  top: 0;
  background: #ffffff;
  color: #7e4b8b;
  box-shadow: 0 12px #883e9b, 0 18px rgba(0, 0, 0, 0.4);
}

.btn.disabled {
  cursor: default;
}

.btn.disabled:hover {
  top: 0;
  background: #ffffff;
  color: #7e4b8b;
  box-shadow: 0 12px #883e9b, 0 18px rgba(0, 0, 0, 0.4);
}

.btn:hover {
  background-color: #7e4b8b;
  color: #0e80b0;
  top: 4px;
  box-shadow: 0 8px #883e9b, 0 14px rgba(0, 0, 0, 0.4);
}

.btn:active {
  top: 8px;
  box-shadow: 0 4px #883e9b, 0 10px rgba(0, 0, 0, 0.4);
}

.game {
  display: flex;
  flex-wrap: wrap;
  width: 850px;
  justify-content: space-evenly;
  align-items: end;
}

.game-info {
  margin: 15px;
  margin-top: 0px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  font-size: 25px;
  color: #A28925;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  height: 290px;
  width: 350px;
}

.board>div {
  display: flex;
}

.game-title {
  font-size: 64px;
  font-family: monospace;
  font-weight: 800;
  color: #883e9b;
  text-align: center;
  margin-bottom: 78px;
}

.game-status {
  font-size: 36px;
  font-weight: bold;
  font-family: monospace;
  margin: 0;
  padding: 0;
  color: #883e9b;
  margin-left: 15px;
  margin-bottom: 30px;
}

/* width */
::-webkit-scrollbar {
  width: 20px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #883e9b; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #301636; 
}

@media screen and (max-width: 768px) {
.game {
  display: contents;
}
.game-info {
  margin-top: 30px;
}
}